import { createElement as creact } from 'react';
import { cssFactory } from '../common/css';

const css = cssFactory('app');

function Header() {
    return (
        creact('div', { className: css('heading') }, [
            /* creact('div', { key: 'A', className: join(css('heading-upper') + siteCss('text-primary')) }, "~~~~~~~~~~"), */
            creact('h1', { key: 'B', className: css('heading-lower') }, "Italienisch lernen")
        ])
    );
}

export default Header;



