import React, { Component } from 'react';
import { siteCss, cssFactory } from '../common/css';

import { addresse, telefon, email } from '../site/data';

import '../common/icon.css';
const css = cssFactory('icon');

class Footer extends Component {
    render() {
        return (
            <div className={siteCss('table', 'wrap-left')}>
                <div className={css('icon', 'as-trow', 'copyright')}><span>Italienisch Lernen - Debora Difonzo</span></div>
                <div className={css('icon', 'as-trow', 'home')}><span>{addresse.strasse}<br />{addresse.ort}</span></div>
                <div className={css('icon', 'as-trow', 'telephone')}><span>{telefon}</span></div>
                <div className={css('icon', 'as-trow', 'email')}><span>{email}</span></div>
            </div>
        );
    }
}

export default Footer;



